@font-face {
  font-family: "Opensans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Opensans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
*,
*::after,
*::before {
  box-sizing: border-box;
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: 1150px;
  padding-right: 30px;
  padding-left: 30px;
}
@media screen and (min-width: 768px) {
  .container {
    padding-right: 50px;
    padding-left: 50px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  font-family: "Opensans", "Arial", sans-serif;
  background-color: #ffffff;
  padding: 16px;
  color: #444444;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;
  border: none;
  box-shadow: inset 0 0 0 2px #444444;
}
@media screen and (min-width: 768px) {
  .button {
    font-size: 20px;
    line-height: 20px;
  }
}
.button:hover {
  background-color: #444444;
  color: #ffffff;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}

.page {
  font-family: "Opensans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  background-color: #ffffff;
}

.block-decorated {
  position: relative;
}

.block-decorated--gray-waves::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 6px;
  background-image: url("../img/svg/waves-gray-mobile.svg");
  background-repeat: repeat-x;
  bottom: 0;
  left: 0;
}
@media screen and (min-width: 768px) {
  .block-decorated--gray-waves::before {
    height: 17px;
    background-image: url("../img/svg/waves-gray.svg");
  }
}
@media screen and (min-width: 1150px) {
  .block-decorated--gray-waves::before {
    width: calc(100% - 100px);
    left: unset;
    height: 23px;
    background-image: url("../img/svg/waves-gray-desktop.svg");
  }
}

@media screen and (min-width: 768px) {
  .block-decorated--white-waves::after {
    position: absolute;
    z-index: 1;
    content: "";
    width: 100%;
    height: 24px;
    bottom: -1px;
    background-image: url("../img/svg/waves-white.svg");
    background-repeat: repeat-x;
    background-position: center;
  }
}
@media screen and (min-width: 1150px) {
  .block-decorated--white-waves::after {
    background-image: url("../img/svg/waves-white-desktop.svg");
    height: 34px;
  }
}

