    display: grid;
    grid-template-columns: 1fr 1fr;
    background-color: transparent;
  }
  .main-nav__list--site-nav {
    grid-column: 2/3;
    grid-row: 1/2;
    grid-template-columns: 84px 1fr;
    padding-right: 50px;
