  }
}
@media screen and (min-width: 1150px) {
  .main-nav__list {
    background-color: transparent;
    max-width: 100%;
  }
  .main-nav__list--site-nav {
    grid-column: 3/4;
    grid-row: 1/2;
  }
}

@media screen and (min-width: 768px) {
  .main-nav__list--main-nav {
    padding-left: 50px;
    background-color: transparent;
    column-gap: 15px;
  }
  .main-nav__list--main-nav .main-nav__link {
    padding: 21px 0 19px 0;
  }
}
@media screen and (min-width: 1150px) {
