  top: 32px;
  width: 20px;
  height: 14px;
  background-image: url("../img/svg/open-menu.svg");
}

.pop-up-menu {
  display: none;
}
@media screen and (min-width: 768px) {
  .pop-up-menu {
    display: grid;
    grid-template-columns: 48% 52%;
    grid-auto-flow: dense;
    width: 100%;
  }
}
@media screen and (min-width: 1150px) {
  .pop-up-menu {
    grid-template-columns: 450px 1fr 400px;
  }
}
.no-js .pop-up-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .no-js .pop-up-menu {
    padding-top: 0;
    display: grid;
  }
}
.pop-up-menu::before {
  content: "";
  width: 100%;
}
@media screen and (min-width: 768px) {
  .pop-up-menu::before {
    grid-column: 1/2;
    grid-row: span 1;
    padding-left: 50px;
  }
}
@media screen and (min-width: 1150px) {
  .pop-up-menu::before {
